import React, { useState, useEffect } from 'react';
import './Forma.css';
import axios from 'axios';
import InputMask from 'react-input-mask';

const Forma = () => {
    const [isVisible, setIsVisible] = useState(false);
    const [hasAnimated, setHasAnimated] = useState(false);
    const [isFormVisible, setIsFormVisible] = useState(true);

    const [isFormValid, setIsFormValid] = useState(false); //     

    useEffect(() => {
        const handleScroll = () => {
            const scrollPosition = window.scrollY + window.innerHeight;
            const formPosition = document.querySelector('.discountForm').offsetTop;

            if (scrollPosition > formPosition) {
                setIsVisible(true);

                if (!hasAnimated) {
                    setHasAnimated(true);
                }
            } else {
                setIsFormVisible(true);
                setIsVisible(false);
            }
        };

        window.addEventListener('scroll', handleScroll);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, [hasAnimated]);

    useEffect(() => {
        if (!isFormVisible && isVisible) {
            setHasAnimated(false);
        }
    }, [isVisible, isFormVisible]);

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!isFormValid) {
            return; //   ,    
        }

        try {
            //    
            await axios.post('http://localhost:4000/api/user', formData);

            //     
            setFormData(initialState);
        } catch (error) {
            console.error(':', error);
        }
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value
        }));
    };
    const initialState = {
        name: '',
        phone: '',
        email: ''
    };

    const [formData, setFormData] = useState(initialState);

    return (
        <div className="discountBlock">
            <div className="discountInfo">
                <h2>     10%</h2>
                <p>   22  2023 .</p>
            </div>
            <div className={`discountForm ${isVisible ? 'active' : ''}`}>
                <form onSubmit={handleSubmit}>
                    <input type="text" name="name" placeholder="" value={formData.name} onChange={handleChange} />
                    {/*  InputMask     */}
                    <InputMask
                        type="tel"
                        mask="+7 (999) 999-99-99"
                        maskChar=" "
                        name="phone"
                        placeholder=" "
                        value={formData.phone}
                        onChange={handleChange}
                        onKeyPress={(e) => {
                            if (isNaN(Number(e.key))) {
                                e.preventDefault();
                            }
                        }}
                    />
                    <input type="text" name="email" placeholder="Email" value={formData.email} onChange={handleChange} />
                    <button type="submit" disabled={!isFormValid}> </button> {/*      */}
                </form>

            </div>
        </div>
    );
};

export default Forma;
