import React from 'react';
import './InfoNumber.css';
const InfoNumber = () => (

    <div className="infoNumber">
        <div>
            <h1>10</h1>
            <span>     </span>
        </div>
        <div>
            <h1>20</h1>
            <span>,   </span>
        </div>
        <div>
            <h1>100</h1>
            <span>    </span>
        </div>
        <div>
            <h1>10 000</h1>
            <span>     </span>
        </div>
    </div>
);

export default InfoNumber;
